.card-input-header {
    display: flex;
    flex-direction: row;
    align-items: center;
}
  
.card-input-header > *,
.card-input-header > input {
    margin: 0;
}
  
.card-input-header .work-button-wrapper {
    margin-left: auto;
}

.card-input-header .top-right {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    top: 40px;
}

.time-buttons {
    &.work {
        margin-bottom: .2em;
    }
}
